











import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Money from '@/components/Money.vue';
import Header from '@/components/Header.vue';
import Tabs from '@/components/Tabs.vue';
import outInList from '@/constant/outInList';
import ECharts from '@/components/statistic/ECharts.vue';
import store from '@/store/index2';

import statisticDateList from '@/constant/statisticDateList';
import tagsList from '@/constant/tagsList';
import {OpUnitType} from 'dayjs';

@Component({
  components: {ECharts, Tabs, Header, Money}
})
export default class Statistic extends Vue {
  outInList = outInList;
  outInValue = '-';
  dateList = statisticDateList;
  dateValue = 'day' as OpUnitType;

  get tagsList() {
    const newTagsList: string[] = [];
    for (let i = 0; i < tagsList.length; i++) {
      newTagsList.push(tagsList[i].value);
    }
    return newTagsList;
  }

  get chartOption() {

    if (!this.chartData || !this.tagsList || !this.chartLabels) {
      return {
        title: {
          text: '',
          top: '5px',
          left: '10px'
        },
      };
    }
    return {
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'shadow'
        }
      },
      legend: {
        data: this.tagsList,
      },
      grid: {
        left: '4%',
        right: '8%',
        bottom: '3%',
        containLabel: true,
      },
      xAxis: {
        type: 'value'
      },
      yAxis: {
        type: 'category',
        data: this.chartLabels,
        width: '1000px'
      },
      series: [
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            position: 'insideRight'
          },
          color: '#fe7547',
          data: this.chartData[0]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            position: 'insideRight'
          },
          color: '#f1d4cb',
          data: this.chartData[1]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            position: 'insideRight'
          },
          color: '#612635',
          data: this.chartData[2]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            position: 'insideRight'
          },
          color: '#a33cbf',
          data: this.chartData[3]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            position: 'insideRight'
          },
          color: '#2bc1ff',
          data: this.chartData[4]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            position: 'insideRight'
          },
          color: '#7b6cff',
          data: this.chartData[5]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            position: 'insideRight'
          },
          color: '#ba0040',
          data: this.chartData[6]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            position: 'insideRight'
          },
          color: '#ea0300',
          data: this.chartData[7]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            position: 'insideRight'
          },
          color: '#5f5f5f',
          data: this.chartData[8]
        },
      ]
    };
  }

  get chartData() {
    return store.fetchChartData(this.outInValue, this.dateValue);
  }

  get chartLabels() {
    return store.fetchChartLabels(this.dateValue);
  }
}
